import React, { useState, useEffect } from "react";
import Select from "react-select";

export default function FormTrajet({ giveCoordsToMap, infosTrajet }) {
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const [searchTermB, setSearchTermB] = useState("");
  const [searchResultsB, setSearchResultsB] = useState([]);
  const [coordsVilleA, setCoordsVilleA] = useState(null); // coordonnes ville A et B
  const [coordsVilleB, setCoordsVilleB] = useState(null);
  const [optionsVilles, setOptionsVilles] = useState([]);
  const [optionsVehicules, setOptionsVehicules] = useState([]);
  const [vehicules, setVehicules] = useState([]); // les vhicules fournies via graphQL et leurs infos
  const [vehicule, setVehicule] = useState(null); // le vhicule choisi dans le select du formulaire (id + nom)
  const [isVillesLoading, setIsVillesLoading] = useState(true);
  const [isVehiclesLoading, setIsVehiclesLoading] = useState(true);
  const [hasErrors, setHasErrors] = useState(false);
  const [infoTrouPaume, setInfoTrouPaume] = useState(false);
  const [disableInputs, setDisableInputs] = useState(false); // lorsque la map est affiche, les inputs sont dsactives

  /**
   * Formate les donnes JSON de l'API pour le composant React Select
   * @param {JSON} villes les villes renvoyes par l'API
   */
  function formatageVilles(villes) {
    var res = [];
    for (let i = 0; i < villes.length; i++) {
      //villes[i].population >= 3000 && // si on veut limiter aux plus grandes villes
      res.push({ value: villes[i].nom, label: villes[i].nom });
    }
    setOptionsVilles(res);
  }

  /**
   * Formate les donnes JSON de l'API pour le composant React Select
   * @param {JSON} vehicules les vhicules renvoyes par l'API
   */
  function formatageVehicules(vehicules) {
    var res = [];
    for (let i = 0; i < vehicules.length; i++) {
      //vehicules[i].population >= 3000 && // si on veut limiter aux plus grandes vehicules
      res.push({
        value: vehicules[i].id,
        label: vehicules[i].naming.make + " " + vehicules[i].naming.model,
        //+` [${vehicules[i].naming.chargetrip_version}]`,
      });
    }
    setOptionsVehicules(res);
  }

  function handleSearchChange(e) {
    const term = e.target.value;
    setSearchTerm(term);

    const results = optionsVilles.filter((ville) =>
      ville.label.toLowerCase().includes(term.toLowerCase())
    );

    term !== "" ? setSearchResults(results) : setSearchResults([]);
  }

  function handleSearchBChange(e) {
    const term = e.target.value;
    setSearchTermB(term);

    const results = optionsVilles.filter((ville) =>
      ville.label.toLowerCase().includes(term.toLowerCase())
    );

    term !== "" ? setSearchResultsB(results) : setSearchResultsB([]);
  }

  function handleVehiculeChange(event) {
    setVehicule(event.value);
  }

  function handleChangeCoordsVille(position) {
    if (position === "dpart") {
      const apiUrl = `https://api-adresse.data.gouv.fr/search/?q=${searchTerm}&type=municipality&limit=1`;
      fetch(apiUrl)
        .then((response) => response.json())
        .then((data) => {
          // Vrifiez si des rsultats ont t retourns
          if (data.features.length > 0) {
            const firstResult = data.features[0];
            const coords = firstResult.geometry.coordinates;

            const latitude = coords[1];
            const longitude = coords[0];

            if (firstResult.properties.population < 1000) {
              setInfoTrouPaume(true);
            } else {
              setInfoTrouPaume(false);
            }

            // console.log(`Latitude: ${latitude}, Longitude: ${longitude}`);
            setCoordsVilleA({ lat: latitude, lon: longitude });
          } else {
            console.log("Aucun rsultat trouv pour la ville spcifie.");
          }
        })
        .catch((error) => {
          console.error("Erreur lors de la requte API Adresse", error);
        });
    } else if (position === "arrive") {
      const apiUrl = `https://api-adresse.data.gouv.fr/search/?q=${searchTermB}&type=municipality&limit=1`;
      fetch(apiUrl)
        .then((response) => response.json())
        .then((data) => {
          // Vrifiez si des rsultats ont t retourns
          if (data.features.length > 0) {
            const firstResult = data.features[0];
            const coords = firstResult.geometry.coordinates;

            const latitude = coords[1];
            const longitude = coords[0];

            if (firstResult.properties.population < 1000) {
              setInfoTrouPaume(true);
            } else {
              setInfoTrouPaume(false);
            }

            // console.log(`Latitude: ${latitude}, Longitude: ${longitude}`);
            setCoordsVilleB({ lat: latitude, lon: longitude });
          } else {
            console.log("Aucun rsultat trouv pour la ville spcifie.");
          }
        })
        .catch((error) => {
          console.error("Erreur lors de la requte API Adresse", error);
        });
    }
  }

  function handleSubmitForm(event) {
    event.preventDefault(); // vite le rechargement de la page
    // transmission des coordonnes  la map une fois les deux villes renseignes
    if (coordsVilleA && coordsVilleB) {
      setHasErrors(false);
      setInfoTrouPaume(false);
      setDisableInputs(true);
      giveCoordsToMap({
        villeA: coordsVilleA,
        villeB: coordsVilleB,
        vehicule: vehicules.find((car) => car.id === vehicule),
      });
    } else {
      setHasErrors(true);
    }
  }

  useEffect(() => {
    // fetch("https://geo.api.gouv.fr/departements/73/communes") => pour tester
    fetch("https://geo.api.gouv.fr/communes")
      .then((response) => response.json())
      .then((data) => {
        //setOptionsVilles(data);
        formatageVilles(data);
        setIsVillesLoading(false); // Les donnes sont rcupres, on met isLoading  false
      })
      .catch((error) => {
        console.error("Erreur lors de la rcupration des donnes :", error);
        setIsVillesLoading(false); // Il y a eu une erreur, on met quand mme isLoading  false
      });
  }, []);

  // rcup des vhicules lectriques
  useEffect(() => {
    const apiUrl = "https://api.chargetrip.io/graphql";
    fetch(apiUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        "x-client-id": "5ed1175bad06853b3aa1e492",
        "x-app-id": "623998b2c35130073829b2d2",
      },
      body: JSON.stringify({
        query: `query vehicleList {
          vehicleList(
            page: 0, 
            size: 20
          ) {
            id
            naming {
              make
              model
              chargetrip_version
            }
            media {
              image {
                thumbnail_url
              }
            }
            battery {
              usable_kwh
            }
            range {
              chargetrip_range {
                best
                worst
              }
            }
          }
        }`,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        setVehicules(data.data.vehicleList),
          formatageVehicules(data.data.vehicleList);
      });
    /*const fetchData = async () => {
      try {
        const headers = {
          //Replace this x-client-id and app-id with your own to get access to more vehicles
          "x-client-id": "5ed1175bad06853b3aa1e492",
          "x-app-id": "623998b2c35130073829b2d2",
          //"x-client-id": "65aa81e90117350bae37ad07",
          //"x-app-id": "65aa81e90117350bae37ad09",
        };
        const apiUrl = "https://api.chargetrip.io/graphql";

        const response = await axios.get(apiUrl, {
          headers: headers,
        });

        setoptionsVehicules(response.data);
      } catch (err) {
        console.log(err);
        setIsVehiclesLoading(true);
      } finally {
        setIsVehiclesLoading(false);
      }
    };

    fetchData();*/
    /*clientQL
      .query({ query: GET_ELECTRIC_VEHICLES })
      .then((result) => {
        setoptionsVehicules(result.data.electricVehicles);
        setIsVehiclesLoading(false);
      })
      .catch((error) => {
        console.log(error);
        setIsVehiclesLoading(true);
      });*/
  }, []);

  return (
    <div>
      <form className="form-floating" onSubmit={handleSubmitForm}>
        {!isVillesLoading ? (
          <div id="villes">
            <div className="form-floating m-3">
              <input
                id="villeA"
                required
                type="search"
                autoComplete="off"
                placeholder="Dpart"
                className="form-control shadow-sm border border-bottom-0"
                onChange={handleSearchChange}
                value={searchTerm}
                disabled={disableInputs}
                style={{
                  width: "150%",
                }}
              />
              <label htmlFor="villeA">Dpart</label>
              <ul
                style={{
                  listStyle: "none",
                  width: "100%",
                  borderRadius: "4px",
                  overflow: "auto",
                  maxHeight: "200px",
                }}
              >
                {searchResults
                  .sort((a, b) => {
                    return a.label.length - b.label.length;
                  })
                  .map(
                    (result, index) =>
                      index < 10 && (
                        <li
                          key={index}
                          onClick={() => {
                            setSearchTerm(result.label);
                            setSearchResults([]);
                            handleChangeCoordsVille("dpart");
                          }}
                          style={{
                            cursor: "pointer",
                            padding: "10px",
                            backgroundColor:
                              index % 2 === 0 ? "#f6f6f6" : "#fff",
                            borderBottom: "1px solid lightgray",
                          }}
                        >
                          {result.label}
                        </li>
                      )
                  )}
              </ul>
            </div>
            <div className="form-floating m-3">
              <input
                id="villeB"
                required
                type="search"
                autoComplete="off"
                placeholder="Arrive"
                className="form-control shadow-sm border border-bottom-0"
                onChange={handleSearchBChange}
                value={searchTermB}
                disabled={disableInputs}
                style={{ width: "150%" }}
              />
              <label htmlFor="villeB">Arrive</label>
              <ul
                style={{
                  listStyle: "none",
                  width: "100%",
                  borderRadius: "4px",
                  overflow: "auto",
                  maxHeight: "200px",
                }}
              >
                {searchResultsB
                  .sort((a, b) => {
                    return a.label.length - b.label.length;
                  })
                  .map(
                    (result, index) =>
                      index < 10 && (
                        <li
                          key={index}
                          onClick={() => {
                            setSearchTermB(result.label);
                            setSearchResultsB([]);
                            handleChangeCoordsVille("arrive");
                          }}
                          style={{
                            cursor: "pointer",
                            padding: "10px",
                            backgroundColor:
                              index % 2 === 0 ? "#f6f6f6" : "#fff",
                            borderBottom: "1px solid lightgray",
                          }}
                        >
                          {result.label}
                        </li>
                      )
                  )}
              </ul>
            </div>
          </div>
        ) : (
          <>
            <div className="spinner-border text-primary" role="status"></div>
            <br />
            <span className="fw-bold">
              Chargement des communes de France...
            </span>
          </>
        )}

        <div className="m-3">
          <Select
            placeholder="Vhicule"
            id="vehicule"
            required
            noOptionsMessage={() => {
              return "Aucune vhicule disponible...";
            }}
            options={optionsVehicules}
            onChange={handleVehiculeChange}
            isDisabled={disableInputs}
            className="shadow-sm"
          />
        </div>
        {vehicule && (
          <img
            className="img-thumbnail rounded mb-2"
            src={
              vehicules.find((car) => car.id === vehicule).media.image
                .thumbnail_url
            }
            alt="un vhicule lectrique"
          />
        )}
        <br />
        <button
          className="btn btn-success shadow-sm"
          type="submit"
          disabled={!coordsVilleA || !coordsVilleB || !vehicule}
        >
          Valider
        </button>
      </form>
      {hasErrors && (
        <div
          className="alert alert-danger mt-4 mx-5 align-items-center shadow-sm"
          role="alert"
        >
          Les donnes des villes ne sont pas correctes
        </div>
      )}
      {infoTrouPaume && (
        <>
          {/*<div className="alert alert-info mt-4 mx-5 d-flex align-items-center shadow-sm" role="alert">
            Attention : Les coordonnes que vous avez entres correspondent  un
            lieu isol ou peu connu. Il se peut que les rsultats de la
            recherche soient imprcis ou errons. Veuillez vrifier la validit
            des donnes avant de les utiliser. Merci de votre comprhension.
      </div>*/}
          <div
            className="alert alert-info mt-4 mx-5 d-flex align-items-center shadow-sm"
            role="alert"
          >
            Attention : Vous avez tap les coordonnes dun bled paum o mme
            les mouches ne vont pas. Il y a de fortes chances que les rsultats
            soient bidons ou  ct de la plaque. Ne faites pas confiance aux
            donnes sans les vrifier. Cest pas de ma faute, cest la vtre.
          </div>
        </>
      )}
      <div className={"mt-3" + (infosTrajet ? "" : " placeholder-glow")}>
        <p className={"fw-bold" + (infosTrajet ? "" : " placeholder")}>
          {"Temps de trajet : "}
          {infosTrajet &&
            (parseFloat(infosTrajet.temps_trajet) >= 1.0
              ? parseFloat(infosTrajet.temps_trajet).toFixed(2) + " h"
              : (parseFloat(infosTrajet.temps_trajet) * 60).toFixed(0) +
                " min")}
        </p>
        {infosTrajet && (
          <>
            <a
              data-bs-toggle="collapse"
              href="#infosTrajetPlus"
              role="button"
              aria-expanded="false"
              aria-controls="infosTrajetPlus"
            >
              Voir plus d'informations
            </a>
            <div className="collapse" id="infosTrajetPlus">
              <div className="card card-body">
                <ul className="list-group">
                  <li className="list-group-item">
                    Autonomie : {infosTrajet.autonomie} km
                  </li>
                  <li className="list-group-item">
                    Temps de charge complte :{" "}
                    {parseFloat(infosTrajet.tps_chargement).toFixed(2)} h
                  </li>
                  <li className="list-group-item">
                    Distance : {parseFloat(infosTrajet.distance).toFixed(2)} km
                  </li>
                  <li className="list-group-item">
                    Vitesse moyenne :{" "}
                    {parseFloat(infosTrajet.vitesse_moyenne).toFixed(2)} km/h
                  </li>
                </ul>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
